:root {
    --color-sky-light: #ffd2bc;
    --color-sky-dark: #f8b9ae;
    --color-tree_dark: #365e57;
    --color-rock_dark: #2f484f;
    --color-rock_light: #917472;
    --color-flame_yellow: #fde26c;
    --color-flame_orange: #fda263;
    width: 100%;
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    transition: all 0.2s ease;
    width: 100%;
}
.h1 {
    margin: 0;
    padding: 0;
    width: 45%;
    position: absolute;
    top: 10px;
    left: 10px;
    font-size: 14px;
    animation: typing 2s steps(22), blink .5s step-end infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
    font-family: monospace;
}

@keyframes typing {
    from {
        width: 0;
    }
}

@keyframes blink {
    50% {
        border-color: transparent;
    }
}

*:before,
*:after {
    content: "";
    position: absolute;
}
.container {
    background: linear-gradient(var(--color-sky-light), var(--color-sky-dark));
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.container:before {
    width: 100%;
    height: 100vh;
    background-image: url(data:image/png;base64,);
    opacity: 0.4;
    z-index: 1000;
}
.campfire_wrapper {
    width: 100%;
    height: 400px;
    position: relative;
}
.tree_container_front,
.tree_container_back {
    bottom: 0;
    height: 200px;
    position: absolute;
}
.tree_container_front {
    width: 390px;
}
.tree_container_back {
    width: 110px;
    right: 0;
}
.tree_container_front > div,
.tree_container_back > div {
    border-bottom-color: var(--color-tree_dark);
    border-bottom-style: solid;
    position: absolute;
    width: 0;
    height: 0;
    border-left-color: transparent;
    border-right-color: transparent;
    border-right-style: solid;
    border-left-style: solid;
}
.tree_container_front > div:before,
.tree_container_back > div:before {
    border-top-color: var(--color-tree_dark);
    border-top-style: solid;
    position: absolute;
    width: 0;
    height: 0;
    border-left-color: transparent;
    border-right-color: transparent;
    border-right-style: solid;
    border-left-style: solid;
    transform: skewX(-70deg);
    opacity: 0.5;
}
.tree_1,
.tree_2,
.tree_10 {
    border-left-width: 6px;
    border-right-width: 6px;
    border-bottom-width: 32px;
    bottom: 68px;
    left: 80px;
}
.tree_1:before,
.tree_2:before,
.tree_10:before {
    margin-top: 32px;
    border-left-width: 6px;
    border-right-width: 6px;
    border-top-width: 13px;
    left: -24px;
}
.tree_2 {
    left: 160px;
}
.tree_3,
.tree_4,
.tree_5,
.tree_6,
.tree_7,
.tree_8,
.tree_9 {
    border-left-width: 6px;
    border-right-width: 6px;
    border-bottom-width: 56px;
    bottom: 72px;
    left: 172px;
}
.tree_3:before,
.tree_4:before,
.tree_5:before,
.tree_6:before,
.tree_7:before,
.tree_8:before,
.tree_9:before {
    margin-top: 56px;
    border-left-width: 6px;
    border-right-width: 6px;
    border-top-width: 23px;
    left: -38px;
}
.tree_4 {
    bottom: 64px;
    left: 238px;
}
.tree_4:before {
    border-top-width: 13px;
    left: _24px;
}
.tree_5 {
    bottom: 76px;
    left: 334px;
}
.tree_5:before {
    border-top-width: 18px;
    left: -31px;
}
.tree_6 {
    bottom: 100px;
    left: 362px;
}
.tree_6:before {
    border-top-width: 12px;
    left: -22px;
}
.tree_7 {
    bottom: 110px;
    left: 368px;
}
.tree_7:before {
    border-top-width: 15px;
    left: -26px;
}
.tree_8 {
    bottom: 120px;
    left: 6px;
}
.tree_9 {
    bottom: 115px;
    left: 12px;
}
.tree_9:before,
.tree_8:before {
    border-top-width: 0px;
}
.tree_10 {
    bottom: 130px;
    left: 34px;
}
.tree_10:before {
    border-top-width: 10px;
    left: -20px;
}
.rock_container {
    position: absolute;
    width: 180px;
    height: 100px;
    bottom: 66px;
    right: 40px;
}
.rock_big {
    position: absolute;
    height: 38px;
    width: 74px;
    border-radius: 74px 74px 0 0;
    left: 80px;
    bottom: 46px;
    background-image: radial-gradient(
        circle at bottom right,
        var(--color-rock_light) 20%,
        var(--color-rock_dark) 60%
    );
}
.rock_big:before {
    position: absolute;
    background-color: var(--color-rock_light);
    transform: skewX(-70deg);
    opacity: 0.6;
    margin-top: 38px;
    height: 18px;
    width: 74px;
    border-radius: 0 0 74px 74px;
    left: -25px;
}
.rock_small > div {
    position: absolute;
    height: 6px;
    width: 12px;
    border-radius: 6px 6px 0 0;
}
.rock_small > div:before {
    position: absolute;
    background-color: var(--color-rock_light);
    transform: skewX(-70deg);
    opacity: 0.5;
}
.rock_1,
.rock_2,
.rock_3,
.rock_4 {
    background-image: radial-gradient(
        circle at bottom right,
        var(--color-rock_light) 20%,
        var(--color-rock_dark) 90%
    );
    left: 70px;
    bottom: 44px;
}
.rock_1:before,
.rock_2:before,
.rock_3:before,
.rock_4:before {
    margin-top: 6px;
    height: 6px;
    width: 12px;
    border-radius: 0 0 6px 6px;
    left: -8px;
}
.rock_2,
.rock_3 {
    background-image: radial-gradient(
        circle at bottom right,
        var(--color-rock_light) 60%,
        var(--color-rock_dark)
    );
}
.rock_2 {
    left: 48px;
    bottom: 36px;
}
.rock_3 {
    left: 30px;
    bottom: 28px;
}
.rock_4 {
    left: 24px;
    bottom: 16px;
}
.smoke_container {
    position: absolute;
    height: 630px;
    width: 40%;
    bottom: 86px;
    left: 55%;
    -webkit-mask-image: linear-gradient(to top, black 30%, rgba(0, 0, 0, 0));
    -webkit-mask-size: 100% 100%;
    -webkit-mask-repeat: no-repeat;
    -webkit-mask-position: center bottom;
}
.fire_container {
    position: absolute;
    width: 42px;
    height: 42px;
    border-radius: 50%;
    background: transparent;
    box-shadow: 0 0 30px rgba(255, 255, 255, 0.7);
    bottom: 0px;
    left: 30px;
    transform-origin: center center;
    transform: rotate(-5deg);
    animation: flicker 3s ease alternate infinite;
}
.flame_1 {
    width: 0;
    height: 0;
    border: 10px solid transparent;
    border-bottom: 34px solid var(--color-flame_yellow);
    position: absolute;
    top: -30px;
    left: 10px;
    opacity: 0.4;
}
.flame_1:after {
    content: "";
    position: absolute;
    left: -10px;
    top: 34px;
    width: 0;
    height: 0;
    border: 10px solid transparent;
    border-top: 16px solid var(--color-flame_yellow);
}
.flame_2 {
    width: 0;
    height: 0;
    border: 4px solid transparent;
    border-bottom: 12px solid var(--color-flame_orange);
    position: absolute;
    top: 6px;
    left: 16px;
    opacity: 0.8;
}
.flame_2:after {
    content: "";
    position: absolute;
    left: -4px;
    top: 12px;
    width: 0;
    height: 0;
    border: 4px solid transparent;
    border-top: 8px solid var(--color-flame_orange);
}
.flame_3 {
    width: 0;
    height: 0;
    border: 1px solid transparent;
    border-bottom: 4px solid var(--color-sky-light);
    position: absolute;
    top: 20px;
    left: 19px;
    opacity: 0.9;
}
.flame_3:after {
    content: "";
    position: absolute;
    left: -1px;
    top: 4px;
    width: 0;
    height: 0;
    border: 1px solid transparent;
    border-top: 4px solid var(--color-sky-light);
}
@keyframes flicker {
    0% {
        transform: rotate(-1deg);
    }
    20% {
        transform: rotate(1deg);
    }
    40% {
        transform: rotate(-1deg);
    }
    60% {
        transform: rotate(1deg) scale(1.1);
    }
    80% {
        transform: rotate(-2deg) scale(1);
    }
    100% {
        transform: rotate(1deg);
    }
}
svg {
    position: absolute;
    top: 0;
    height: 600px;
    width: 200px;
}
svg path {
    fill: none;
    stroke: white;
    opacity: 0.3;
    stroke-width: 5px;
    stroke-dasharray: 500 100;
    stroke-linecap: round;
    animation: dash 3s linear infinite;
}
@keyframes dash {
    from {
        stroke-dashoffset: 0;
    }
    to {
        stroke-dashoffset: 600;
    }
}
